import React from "react";
import PropTypes from "prop-types";
import "../styles.css";

let Event = ({ onClick, selected, content, submit }) => {

  return (
    <li
      onClick={onClick}
      className={!selected ? "event" : "event event--active"}
    >
      <form
        onSubmit={submit}
      >
        <input defaultValue={content} className="event-input"></input>
      </form>
    </li>
  );
};

Event.propTypes = {
  onClick: PropTypes.func.isRequired,
  selected: PropTypes.bool.isRequired,
  content: PropTypes.string.isRequired
};


export default Event;



