import { connect } from "react-redux";
import { toggleEvent, VisibilityFilters } from "../redux/actions";
import EventList from "../components/EventList";
import { updateEvent } from "../redux/actions";

const getVisibileEvents = (events, filter) => {
  switch (filter) {
    case VisibilityFilters.SHOW_SELECTED:
      return events.filter(e => e.selected);
    case VisibilityFilters.SHOW_ACTIVE:
      return events.filter(e => !e.selected);
    case VisibilityFilters.SHOW_ALL:
    default:
      return events;
  }
};

const mapStateToProps = state => {
  return {
    events: getVisibileEvents(state.events, state.visibilityFilter)
  };
};

const mapDispatchToProps = dispatch => {
  return {
    toggleEvent: id => dispatch(toggleEvent(id)),
    updateEvent: (content, id) => dispatch(updateEvent(content, id))
  };
};


const VisibleEventList = connect(
  mapStateToProps,
  mapDispatchToProps
)(EventList);

export default VisibleEventList;



